<template>
  <div>

    <div style="margin: 10px 0">
      <el-button type="primary" @click="handleAdd">新增 <i class="el-icon-circle-plus-outline"></i></el-button>
      <el-popconfirm
          class="ml-5"
          confirm-button-text='确定'
          cancel-button-text='我再想想'
          icon="el-icon-info"
          icon-color="red"
          title="您确定批量删除这些数据吗？"
          @confirm="delBatch"
      >
      </el-popconfirm>
      <el-button type="primary" @click="exportProduct(1)">导出送货单 <i class="el-icon-top"></i></el-button>
      <el-button type="primary" @click="exportProduct(0)">导出报价单 <i class="el-icon-top"></i></el-button>
    </div>
    <el-dialog :visible.sync="dialogVisible">
      <img :src="imageUrl" style="max-width: 100%; max-height: 100%;">
    </el-dialog>
    <el-table
        :data="tableData"
        border
        stripe
        :highlight-current-row="true"
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>

      <el-table-column prop="name" label="商品名称">
      </el-table-column>
      <el-table-column prop="specification" label="商品规格">
      </el-table-column>
      <el-table-column prop="price" label="商品价格">
      </el-table-column>
      <el-table-column label="数量" :scoped-slot="scopedSlot">
        <template slot-scope="{ row }">
          <el-input v-model="row.number"></el-input>
        </template>
      </el-table-column>


      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="success" @click="handleEdit(scope.row)">编辑 <i class="el-icon-edit"></i></el-button>
          <el-popconfirm
              class="ml-5"
              confirm-button-text='确定'
              cancel-button-text='我再想想'
              icon="el-icon-info"
              icon-color="red"
              title="您确定删除吗？"
              @confirm="del(scope.row.id)"
          >
            <el-button type="danger" slot="reference">删除 <i class="el-icon-remove-outline"></i></el-button>
          </el-popconfirm>
        </template>
      </el-table-column>

    </el-table>
    <!--    增加，修改表单-->
    <el-dialog title="商品" :visible.sync="dialogFormVisible" width="60%">
      <el-form size="small" style="max-height: 60vh;overflow-y: auto;">
        <el-form-item label="id" hidden>
          <el-input v-model="form.id" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品名称">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品规格">
          <el-input v-model="form.specification" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品价格">
          <el-input v-model="form.price" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="明细备注">
          <el-input v-model="form.remark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>


  </div>
</template>

<script>
import axios from '@/request/index.js'
import {serverIp} from "../../../public/config";

export default {
  name: "article",
  data() {

    return {
      imageUrl: "",
      dialogVisible: false,
      serverIp: serverIp,
      tableData: [],
      pageSize: 5,
      pageNum: 1,
      total: 0,
      title: "",
      dialogFormVisible: false,
      form: {},
      multipleSelection: [],
      content: '',
      viewDialogVis: false,
      categoryList: []

    }
  },
  computed: {
    headers() {
      return {
        token: `${this.token}`,
      };
    },
    pictureUrl() {
      return this.form.pictureUrl
    },

  },
  methods: {

    showDialog(url) {
      this.dialogVisible = true
      this.imageUrl = url
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    handleAvatarSuccess(res) {
      // console.log(res)
      this.form.pictureUrl = res.data
      this.pictureUrl = res.data
      console.log(this.form.pictureUrl)
    },
    view(content) {
      this.content = content
      this.viewDialogVis = true
    },
    /**
     * 刷新列表
     */
    load() {
      axios.post('commodity/getAllCommodity').then((res) => {
        console.log(res)
        this.tableData = res.data.data;
      })
    },

    handleSelectionChange(val) {
      console.log(val)
      this.multipleSelection = val
    },
    delBatch() {
      let ids = this.multipleSelection.map(v => v.id)  // [{}, {}, {}] => [1,2,3]
      axios.post("/article/del/batch", ids).then(res => {
        if (res.data.code === 200) {
          this.$message.success("批量删除成功")
          this.load()
        } else {
          this.$message.error("批量删除失败")
        }
      })
    },


    handleAdd() {
      this.dialogFormVisible = true
      this.form = {}
    },
    save() {
      axios.post("/commodity/operate", this.form).then(res => {
        console.log(res)
        if (res.data.code === 200) {
          this.$message.success("保存成功")
          this.dialogFormVisible = false
          this.load()
        } else {
          this.$message.error("保存失败")
        }
      })
    },
    handleEdit(row) {
      this.form = row
      this.dialogFormVisible = true
    },
    del(id) {
      axios.post("/commodity/operate", {
        id: id,
        deleteFlag:"1"

      }).then(res => {
        if (res.data.code === 200) {
          this.$message.success("删除成功")
          this.load()
        } else {
          this.$message.error("删除失败")
        }
      })
    },
    exportProduct(type) {
      axios.post("/commodity/generateExcel", {
        type: type,
        sheetReqList: this.multipleSelection,
      }, {
        responseType: 'blob' // 设置响应类型为 Blob
      }).then(res => {
        this.handleMaterialDownload(res,type)
      })
    },
    handleMaterialDownload(data,type) {
      debugger;
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data.data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      if(type==1){
        link.setAttribute("download", `送货单.xlsx`);
      }else {
        link.setAttribute("download", `报价单.xlsx`);

      }
      document.body.appendChild(link);
      link.click();
    }
  },
  created() {
    this.load();
  },

}
</script>

<style scoped>
.avatar-uploader {
  padding-bottom: 10px;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 138px;
  height: 138px;
  line-height: 138px;
  text-align: center;
}

.avatar {
  width: 138px;
  height: 138px;
  display: block;
}
</style>
